import React from 'react';
import '../css/News.css';

function News() {
  return (
    <div className="news d-inline-flex justify-content-between border-bottom border-top">
        <div className="news-1 d-inline-flex">
            <img src={require("../../images/news1.jpg")} alt="news-1"/>
            <span>Oct 18<br></br> I would like to avoid making these mistakes.</span>
        </div>
        <div className="news-2 d-inline-flex">
            <img src={require("../../images/news2.jpg")} alt="news-2"/>
            <span>Oct 8<br></br> But how do you avoid mistakes you make by default?</span>
        </div>
        <div className="news-3 d-inline-flex">
            <img src={require("../../images/news3.jpg")} alt="news-3"/>
            <span>Oct 2<br></br> Ideally you transform your life so it has other defaults.</span>
        </div>
    </div>
  );
}

export default News;