import React from 'react';
import '../css/Content.css';
let click=false;
function showContent()
{
  if(click==false)
  {
    document.getElementById("show").style.display="block";
    document.getElementById("hide").style.display="none";
    click=true;
  }
  else
  {
    document.getElementById("show").style.display="none";
    document.getElementById("hide").style.display="block";
    click=false;
  }
}

function Content() {
  return (
    <div className="content row m-0 text-center">
        <div className="content-1 m-auto" id="hide">
          <h1 className="m-auto">Don't ignore your dreams</h1>
          <p className="title ml-auto mr-auto mb-0 pt-5"><b>The 5 regrets</b> paint a portrait of post-industrial man, who shrinks
         himself into a shape that fits his circumstances, then turns dutifully till he stops</p>
        </div>
        <div className="content-2 m-auto" id="show">
          <h1 className="m-auto">Don't ignore your dreams</h1>
          <p className="title ml-auto mr-auto mb-0"><b>The 5 regrets</b> paint a portrait of post-industrial man, who shrinks
         himself into a shape that fits his circumstances, then turns dutifully till he stops</p>
        </div>
         <div className="button m-0">
            <button className="mr-3"><a onClick={showContent}>See how it works</a></button>
            <button className="ml-3">Join us</button>
         </div>
    </div>
  );
}

export default Content;