import React from 'react';
import '../css/Navbar.css';

function Navbar() {
  return (
    <nav className="row p-0 m-0 justify-content-between">
        <div className="logo ml-4 mt-3 mb-3 col-sm-3">
            <img src={require("../../images/logo.png")} alt="logo" />
            <span> your nice slogan</span>
        </div>
        <div className="menu col-sm-4 text-center">
            <ul className="row h-100 mr-4">
                <li className="col-sm-5 p-0"><a className="d-flex w-100 h-100 justify-content-center align-items-center" href="#home"> How it works</a></li>
                <li className="col-sm-4 p-0"><a className="d-flex w-100 h-100 justify-content-center align-items-center" href="#home"> Sign up</a></li>
                <li className="col-sm-3 p-0"><a className="d-flex w-100 h-100 justify-content-center align-items-center" href="#home"> Login</a></li>
            </ul>
        </div>
    </nav>
  );
}

export default Navbar;