import React from 'react';
import '../css/Slider.css';

function Slider() {
  return (
    <div className="slider d-inline-flex justify-content-between ml-0 mr-0">
        <div className="slider-1">
            <img src={require("../../images/photo1.jpg")} alt="slider-1" className="m-3"/>
            <div className="d-inline-flex ml-3 mb-3">
                <img src={require("../../images/person1.jpg")} alt="slider-1" className="mr-2"/>
                <span><b>You neglect your friends</b><br/><span className="name">Valerie Adams. Moldova. 19.</span></span>
            </div>
        </div>
        <div className="slider-2">
            <img src={require("../../images/photo2.jpg")} alt="slider-2" className="m-3"/>
            <div className="d-inline-flex ml-3 mb-3">
                <img src={require("../../images/person2.jpg")} alt="person-2" className="mr-2"/>
                <span><b>You forget your dreams</b><br/><span className="name"> Robert. Italy. 38</span> </span>
            </div>
        </div>
        <div className="slider-3">
            <img src={require("../../images/photo3.jpg")} alt="slider-3" className="m-3"/>
            <div className="d-inline-flex ml-3 mb-3">
                <img src={require("../../images/person3.jpg")} alt="person-3" className="mr-2"/>
                <span><b>You suppress your feelings</b><br/><span className="name"> Sergei. Italy. 38</span> </span>
            </div>
        </div>
    </div>
  );
}

export default Slider;